import { ITracker } from './ITracker';

export class BroadcastingTracker implements ITracker {
    private readonly trackers: ITracker[];

    constructor(trackers: ITracker[]) {
        this.trackers = trackers;
    }

    public async trackPageView(name: string, url: string, data?: {[key: string]: any}): Promise<void> {
        for (let tracker of this.trackers) {
            await tracker.trackPageView(name, url, data);
        }
    }

    public async trackEvent(name: string, data?: {[key: string]: any}, duration?: number): Promise<void> {
        for (let tracker of this.trackers) {
            await tracker.trackEvent(name, data, duration);
        }
    }
}
