import React, { useEffect, useState } from 'react';
import { TextField, PrimaryButton, Button, Pivot, PivotItem, Label, Checkbox, Stack, ChoiceGroup, Spinner, SpinnerSize, Text, Link } from 'office-ui-fabric-react';
import useSettingsForm from './useSettingsForm';

import styles from './Form.module.scss';
import { SettingsKeys } from '../services/SettingsKeys';
import { useConstCallback } from '@uifabric/react-hooks';
import { Constants } from '../Constants';

function Form(props: { dismiss: () => void }) {
  const {
    inputs,
    isLoading,
    isSaving,
    error,
    handleSubmit,
    handleInputChange,
    handleCheckboxChange,
    handleChoiceGroupChangeBoolean,
    setValue
  } = useSettingsForm(props.dismiss);
  const [enableTrackingCheckbox, setEnableTrackingCheckbox] = useState<boolean>(false);

  useEffect(() => {
    if (!isLoading && !error) {
      const state = inputs.PiwikStatus === "Adding" || inputs.PiwikStatus === "New" ||
        ((!inputs.PiwikStatus || (inputs.PiwikStatus !== "Deleting" && inputs.PiwikStatus !== "Deactivating")) && inputs.IsTrackingActive);
      setEnableTrackingCheckbox(state);
    }
  }, [inputs, error, isLoading]);

  const setEnableTracking = useConstCallback((ev?: React.FormEvent<HTMLElement | HTMLInputElement>, checked?: boolean): void => {
    ev!.persist();
    setValue(SettingsKeys.PiwikStatus, checked ? "Adding" : "Deleting");
  });

  return (
    <>
      {isLoading && <Spinner size={SpinnerSize.large} styles={{ root: { textAlign: "center", marginTop: 40 } }} />}
      {!isLoading &&
        <form onSubmit={handleSubmit}>
          <Pivot aria-label="Piwik PRO">
            <PivotItem headerText="Site Collection Settings">
              <Stack tokens={{ childrenGap: 10 }}>
                <Label style={{ fontWeight: "bold", fontSize: 18 }}>General</Label>
                <Text styles={{root: {marginTop: "0 !important"}}}>It may take up to one hour for the changes on this tab to apply for all users.</Text>
                <Checkbox label="Enable tracking" name="EnableTracking" onChange={setEnableTracking} checked={enableTrackingCheckbox}></Checkbox>
                {enableTrackingCheckbox && !inputs.IsTrackingActive &&
                  <Text style={{ color: "#FFB900" }}>The following settings will be available after activation.</Text>
                }
                <TextField label="Piwik PRO Site ID" disabled={true} name="SiteId" value={inputs.SiteId}></TextField>
                {inputs.Url && inputs.SiteId &&
                    <Link
                      href={`${inputs.Url}/analytics/#/${inputs.SiteId}/dashboards`}
                      target="_blank"
                      data-interception="off">Piwik PRO Analytics</Link>
                  }
                <Label style={{ fontWeight: "bold", fontSize: 18 }}>User Information</Label>
                <ChoiceGroup name="UseSiteCollectionSettings" onChange={handleChoiceGroupChangeBoolean} selectedKey={inputs.UseSiteCollectionSettings?.toString()} options={[
                  { key: "false", text: "Use global settings", styles: { root: { display: "inline-block", marginBottom: 5 } } },
                  { key: "true", text: "Overwrite global settings", styles: { root: { display: "inline-block", marginLeft: 30, marginBottom: 5 } } }
                ]} styles={{ applicationRole: { marginTop: 0 } }} disabled={!enableTrackingCheckbox || !inputs.IsTrackingActive} />
                <Checkbox label="Send User ID anonymized" name="SendUserIdEncoded" onChange={handleCheckboxChange} checked={inputs.SendUserIdEncoded} disabled={!enableTrackingCheckbox || !inputs.IsTrackingActive || !inputs.UseSiteCollectionSettings}></Checkbox>
                <Checkbox label="Send extended user information:" name="SendExtendedUserinfo" onChange={handleCheckboxChange} checked={inputs.SendExtendedUserinfo} disabled={!enableTrackingCheckbox || !inputs.IsTrackingActive || !inputs.UseSiteCollectionSettings}></Checkbox>
                <Stack tokens={{ childrenGap: 10 }} style={{ marginLeft: 20 }}>
                  <Checkbox label="Username (display name)" name="SendUserName" onChange={handleCheckboxChange} checked={inputs.SendUserName} disabled={!enableTrackingCheckbox || !inputs.IsTrackingActive || !inputs.UseSiteCollectionSettings || !inputs.SendExtendedUserinfo}></Checkbox>
                  <Checkbox label="Office" name="SendOffice" onChange={handleCheckboxChange} checked={inputs.SendOffice} disabled={!enableTrackingCheckbox || !inputs.IsTrackingActive || !inputs.UseSiteCollectionSettings || !inputs.SendExtendedUserinfo}></Checkbox>
                  <Checkbox label="JobTitle" name="SendJobTitle" onChange={handleCheckboxChange} checked={inputs.SendJobTitle} disabled={!enableTrackingCheckbox || !inputs.IsTrackingActive || !inputs.UseSiteCollectionSettings || !inputs.SendExtendedUserinfo}></Checkbox>
                  <Checkbox label="Department" name="SendDepartment" onChange={handleCheckboxChange} checked={inputs.SendDepartment} disabled={!enableTrackingCheckbox || !inputs.IsTrackingActive || !inputs.UseSiteCollectionSettings || !inputs.SendExtendedUserinfo}></Checkbox>
                </Stack>
              </Stack>
            </PivotItem>
            {inputs.IsCurrentUserPiwikAdmin &&
              <PivotItem headerText="Global Settings">
                <Stack tokens={{ childrenGap: 10 }}>
                  <Label style={{ fontWeight: "bold", fontSize: 18 }}>Default site collection settings</Label>
                  <Checkbox label="Send User ID anonymized" name="TemplateSendUserIdEncoded" onChange={handleCheckboxChange} checked={inputs.TemplateSendUserIdEncoded}></Checkbox>
                  <Checkbox label="Send extended user information:" name="TemplateSendExtendedUserinfo" onChange={handleCheckboxChange} checked={inputs.TemplateSendExtendedUserinfo}></Checkbox>
                  <Stack tokens={{ childrenGap: 10 }} style={{ marginLeft: 20 }}>
                    <Checkbox label="Username (display name)" name="TemplateSendUserName" onChange={handleCheckboxChange} checked={inputs.TemplateSendUserName} disabled={!inputs.TemplateSendExtendedUserinfo}></Checkbox>
                    <Checkbox label="Office" name="TemplateSendOffice" onChange={handleCheckboxChange} checked={inputs.TemplateSendOffice} disabled={!inputs.TemplateSendExtendedUserinfo}></Checkbox>
                    <Checkbox label="JobTitle" name="TemplateSendJobTitle" onChange={handleCheckboxChange} checked={inputs.TemplateSendJobTitle} disabled={!inputs.TemplateSendExtendedUserinfo}></Checkbox>
                    <Checkbox label="Department" name="TemplateSendDepartment" onChange={handleCheckboxChange} checked={inputs.TemplateSendDepartment} disabled={!inputs.TemplateSendExtendedUserinfo}></Checkbox>
                  </Stack>
                </Stack>
              </PivotItem>
            }
            {inputs.IsCurrentUserPiwikAdmin &&
              <PivotItem headerText="Connection Settings">
                <Stack tokens={{ childrenGap: 10 }}>
                  <Label style={{ fontWeight: "bold", fontSize: 18 }}>Piwik PRO Analytics Suite instance connection settings</Label>
                  <Checkbox label="Enforce SSL communication" name="EnforceSslComunication" onChange={handleCheckboxChange} checked={inputs.EnforceSslComunication}></Checkbox>
                  <TextField label="Piwik PRO Instance URL" name="Url" onChange={handleInputChange} value={inputs.Url}></TextField>
                  <TextField label="Piwik PRO Custom Containers URL" name="ContainersUrl" onChange={handleInputChange} value={inputs.ContainersUrl}></TextField>
                  <Checkbox label="Use SHA3 to anonymize UserID (default SHA1)" name="UseSha3" onChange={handleCheckboxChange} checked={inputs.UseSha3}></Checkbox>
                  <TextField label="Piwik PRO Client ID" name="ClientID" onChange={handleInputChange} value={inputs.ClientID}></TextField>
                  <TextField label="Piwik PRO Client Secret" name="ClientSecret" onChange={handleInputChange} value={inputs.ClientSecret}></TextField>
                </Stack>
              </PivotItem>
            }
            {inputs.IsCurrentUserPiwikAdmin &&
              <PivotItem headerText="About">
                <Stack tokens={{ childrenGap: 10 }}>
                  <Label style={{ fontWeight: "bold", fontSize: 18 }}>Versions</Label>
                  <Text>Tracker: {Constants.version}</Text>
                  <Text>Job: {inputs.JobVersion}</Text>
                </Stack>
              </PivotItem>
            }
          </Pivot>
          <div style={{ marginTop: 10 }}>
            <PrimaryButton type="submit" text="Submit" disabled={isSaving} />
            <Button text="Cancel" onClick={() => props.dismiss()} disabled={isSaving} />
          </div>
        </form>
      }
    </>
  );
}

export default Form;
