import "./polyfills";
import React from 'react';
import ReactDOM from 'react-dom';
import { GlobalSettings } from '@uifabric/utilities/lib/GlobalSettings';
import { getTheme } from '@uifabric/styling/lib/styles/theme';
import './index.css';
import App from './App';
import { Tracking } from './tracking/Tracking';
import { Context } from './tracking/contexts/Context';
import * as serviceWorker from './serviceWorker';
import { sp } from '@pnp/sp/presets/all';

const configureTracking = async (): Promise<void> => {
    const tracking = new Tracking(new Context());
    await tracking.init();
}

sp.setup({
    sp: {
        baseUrl: _spPageContextInfo.webAbsoluteUrl
    },
    ie11: true
});

configureTracking();

/**
 * Fix to make it work with SharePoint 2019 and OUIFR 7.x
 */
const customizations = GlobalSettings?.getValue('customizations');
const theme = getTheme();
if (customizations && theme) {
    if (!(customizations as any).settings?.theme?.effects) {
        (customizations as any).settings.theme.effects = { ...theme.effects };
    }
    if (!(customizations as any).settings?.theme?.spacing) {
        (customizations as any).settings.theme.spacing = { ...theme.spacing };
    }
    if (!(customizations as any).settings?.theme?.fonts) {
        (customizations as any).settings.theme.fonts = { ...theme.fonts };
    }
}
/**
 * End of fix
 */

const addPiwikSettings = () => {
    if (_spPageContextInfo.isSiteAdmin !== false) {
        const tryToAddPiwikProSettingsLink = () => {
            const navBarDataLinks = document.querySelector('#O365_Settings_navbardatalinks ul');
            if (navBarDataLinks && !navBarDataLinks.querySelector('.piwikProLink')) {
                const element = document.createElement('li');
                element.className = "piwikProLink";
                navBarDataLinks.append(element);
                ReactDOM.render(<App />, element);
            }

            const settingsButton = document.querySelector('#O365_MainLink_Settings.o365cs-spo-topbarMenuOpen');
            if (settingsButton) {
                const contextMenu = settingsButton.parentElement.querySelector('.contextMenuPopup');
                if (contextMenu && !contextMenu.querySelector('.piwikProLinkContextMenu')) {
                    const element = document.createElement('span');
                    element.className = "piwikProLinkContextMenu o365button o365cs-contextMenuItem ms-fcl-b ms-fcl-b-h ms-fcl-b-f";

                    if (contextMenu.querySelector("[aria-label='Piwik PRO']")) {
                        element.className += " hiddenPiwikLink";
                    }

                    contextMenu.append(element);
                    ReactDOM.render(<App />, element);
                }
            }

            const siteSettingsButton = document.querySelector('#suiteBarButtons .ms-siteactions-root');
            if (siteSettingsButton && !document.querySelector('.piwikProLink')) {
                const element = document.createElement('div');
                element.className = "piwikProLink piwikProLinkSiteActions";
                document.body.append(element);
                ReactDOM.render(<App />, element);
            }
        };

        const settingsElement = document.querySelector("#O365_NavHeader, #suiteBarButtons .ms-siteactions-root");
        settingsElement?.addEventListener("click", () => {
            const interval = setInterval(tryToAddPiwikProSettingsLink, 100);
            setTimeout(() => clearInterval(interval), 1000);
        }, true);
    }
};

addPiwikSettings();

if ((window as any).asyncDeltaManager) {
    (window as any).asyncDeltaManager.add_pageLoaded(() => {
        addPiwikSettings();
    });
}

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
