import { PnPClientStorage } from '@pnp/common';
import { IList, SPHttpClient, Web, sp } from '@pnp/sp/presets/all';
import { ISettings } from './ISettings';
import { ITrackerSettings } from './ITrackerSettings';
import { SettingsKeys } from './SettingsKeys';
import { Constants } from "../Constants";

const ensureSettingsList = async (siteUrl: string, listTitle: string): Promise<IList> => {
    const list = await Web(siteUrl).lists.ensure(listTitle, "", 100, false, { OnQuickLaunch: false });
    if (list.created) {
        await list.list.fields.addText("Value", 255);
        await list.list.defaultView.fields.add("Value");
        await list.list.breakRoleInheritance();
    }
    return list.list;
};

const getSettingsList = async (siteUrl: string, listTitle: string): Promise<{ [key: string]: string }> => {
    const list = await ensureSettingsList(siteUrl, listTitle);
    const items = await list.items.select('Id', 'Title', 'Value')();
    const config: { [key: string]: string } = {};
    for (let item of items) {
        config[item.Title] = item.Value;
    }
    return config;
};

const setSettingsList = async (siteUrl: string, listTitle: string, values: { [key: string]: string }): Promise<void> => {
    const list = await ensureSettingsList(siteUrl, listTitle);
    const items = await list.items.select('Id', 'Title', 'Value')();
    const promises: Promise<any>[] = [];
    for (let key of Object.keys(values)) {
        const existingItem = items.find(x => x.Title === key);
        if (existingItem) {
            if (existingItem.Value !== values[key]) {
                promises.push(list.items.getById(existingItem.Id).update({ Value: values[key] }));
            }
        } else {
            promises.push(list.items.add({ Title: key, Value: values[key] }));
        }
    }
    await Promise.all(promises);
};

const getPropertyBag = async (siteUrl: string, selectProperties?: string[]): Promise<{ [key: string]: string }> => {
    let properties: any;
    if (selectProperties) {
        properties = await Web(siteUrl).allProperties.select(...selectProperties)();
    }
    else {
        properties = await Web(siteUrl).allProperties();
    }

    const result: { [key: string]: string } = {};
    for (let key of Object.keys(properties)) {
        result[key.replace(/_x005f_/gi, "_")] = properties[key];
    }
    return result;
};

const setPropertyBag = async (siteUrl: string, values: { [key: string]: string | boolean }): Promise<void> => {
    // We're using forged client.svc query here as setting property bag via REST API is not supported
    const body = (() => {
        let objectId: number = 4;
        return `
            <Request xmlns="http://schemas.microsoft.com/sharepoint/clientquery/2009" SchemaVersion="15.0.0.0" LibraryVersion="16.0.0.0" ApplicationName="Javascript Library">
                <Actions>
                    ${Object.keys(values).map(key => `
                        <Method Name="SetFieldValue" Id="${objectId++}" ObjectPathId="4">
                            <Parameters>
                                <Parameter Type="String">${key}</Parameter>
                                <Parameter Type="String">${values[key]}</Parameter>
                            </Parameters>
                        </Method>
                    `).join('')}
                    <Method Name="Update" Id="${objectId++}" ObjectPathId="2" />
                </Actions>
                <ObjectPaths>
                    <StaticProperty Id="0" TypeId="{3747adcd-a3c3-41b9-bfab-4a64dd2f1e0a}" Name="Current" />
                    <Property Id="2" ParentId="0" Name="Web" />
                    <Property Id="4" ParentId="2" Name="AllProperties" />
                </ObjectPaths>
            </Request>
        `;
    })();

    const endpoint = `${siteUrl}/_vti_bin/client.svc/ProcessQuery`;
    const client = new SPHttpClient();

    const res = await client.post(endpoint, {
        headers: {
            'Accept': '*/*',
            'Content-Type': 'text/xml;charset="UTF-8"',
            'X-Requested-With': 'XMLHttpRequest'
        },
        body
    });
    const response = await res.json();

    if (response[0].ErrorInfo) {
        throw new Error(response[0].ErrorInfo.ErrorMessage);
    }
};

const getSettings = async (): Promise<ISettings> => {
    const currentUserPiwikAdminPromise = Web(window.location.origin + Constants.piwikAdminServerRelativeUrl)
        .currentUser
        .select("IsSiteAdmin")();
    const propertyBagPromise = getPropertyBag(_spPageContextInfo.siteAbsoluteUrl);
    const settingsListPromise = getSettingsList(_spPageContextInfo.siteAbsoluteUrl, Constants.siteCollectionSettingsListName);
    const piwikProSiteDirectoryPromise = Web(window.location.origin + Constants.piwikAdminServerRelativeUrl)
        .getList("/sites/PiwikAdmin/Lists/PiwikAdmin")
        .getItemsByCAMLQuery({ ViewXml: `<View><Query><Where><Eq><FieldRef Name="pwk_url" /><Value Type="Text">${_spPageContextInfo.siteAbsoluteUrl}</Value></Eq></Where></Query></View>` });

    const currentUserPiwikAdmin = await currentUserPiwikAdminPromise;

    let adminPropertyBag = {};
    let adminSettingsList = {};

    if (currentUserPiwikAdmin.IsSiteAdmin) {
        const adminPropertyBagPromise = getPropertyBag(window.location.origin + Constants.piwikAdminServerRelativeUrl);
        const adminSettingsListPromise = getSettingsList(window.location.origin + Constants.piwikAdminServerRelativeUrl, Constants.globalSettingsListName);
        adminPropertyBag = await adminPropertyBagPromise;
        adminSettingsList = await adminSettingsListPromise;
    }

    const propertyBag = await propertyBagPromise;
    const settingsList = await settingsListPromise;
    const piwikProSiteDirectory = await piwikProSiteDirectoryPromise;

    const data: ISettings = {
        ClientID: adminSettingsList[SettingsKeys.ClientID],
        ClientSecret: adminSettingsList[SettingsKeys.ClientSecret],
        ContainersUrl: adminPropertyBag[SettingsKeys.ContainersUrl],
        EnforceSslComunication: adminPropertyBag[SettingsKeys.EnforceSslComunication] === 'true',
        IsCurrentUserPiwikAdmin: currentUserPiwikAdmin.IsSiteAdmin,
        IsTrackingActive: propertyBag[SettingsKeys.IsTrackingActive] === 'true',
        JobVersion: adminPropertyBag[SettingsKeys.JobVersion],
        PiwikStatus: !settingsList[SettingsKeys.PiwikStatus] || settingsList[SettingsKeys.PiwikStatus] === "Processed" ? piwikProSiteDirectory[0]?.pwk_status : settingsList[SettingsKeys.PiwikStatus],
        SendDepartment: propertyBag[SettingsKeys.SendDepartment] === 'true',
        SendExtendedUserinfo: propertyBag[SettingsKeys.SendExtendedUserinfo] === 'true',
        SendJobTitle: propertyBag[SettingsKeys.SendJobTitle] === 'true',
        SendOffice: propertyBag[SettingsKeys.SendOffice] === 'true',
        SendUserIdEncoded: propertyBag[SettingsKeys.SendUserIdEncoded] === 'true',
        SendUserName: propertyBag[SettingsKeys.SendUserName] === 'true',
        SiteId: propertyBag[SettingsKeys.SiteId],
        TemplateSendDepartment: adminPropertyBag[SettingsKeys.TemplateSendDepartment] === 'true',
        TemplateSendExtendedUserinfo: adminPropertyBag[SettingsKeys.TemplateSendExtendedUserinfo] === 'true',
        TemplateSendJobTitle: adminPropertyBag[SettingsKeys.TemplateSendJobTitle] === 'true',
        TemplateSendOffice: adminPropertyBag[SettingsKeys.TemplateSendOffice] === 'true',
        TemplateSendUserIdEncoded: adminPropertyBag[SettingsKeys.TemplateSendUserIdEncoded] === 'true',
        TemplateSendUserName: adminPropertyBag[SettingsKeys.TemplateSendUserName] === 'true',
        Url: adminPropertyBag[SettingsKeys.Url],
        UseSha3: adminPropertyBag[SettingsKeys.UseSha3] === 'true',
        UseSiteCollectionSettings: propertyBag[SettingsKeys.UseSiteCollectionSettings] === 'true'
    };

    return data;
};

const setSettings = async (settings: ISettings): Promise<void> => {
    const promises: Promise<any>[] = [];

    if (settings.PiwikStatus === "Adding" || settings.PiwikStatus === "Deleting") {
        if (_spPageContextInfo.webAbsoluteUrl.indexOf(".sharepoint.com") > -1) {
            promises.push(setSettingsList(_spPageContextInfo.siteAbsoluteUrl, Constants.siteCollectionSettingsListName, {
                [SettingsKeys.PiwikStatus]: settings.PiwikStatus
            }));
        } else {
            promises.push(fetch(
                `${_spPageContextInfo.siteAbsoluteUrl}/_vti_bin/CUSTOM/PiwikPROChangeTrackingStatus.svc/ChangeTrackingStatus?status=${settings.PiwikStatus === "Adding" ? "0" : "1"}`,
                {
                    method: "POST",
                    headers: {
                        "Accept": "application/json; odata=nometadata",
                        "X-RequestDigest": (await sp.site.getContextInfo())?.FormDigestValue?.toString()
                    }
                }
            ));
        }
    }

    if (settings.IsTrackingActive) {
        promises.push(setPropertyBag(_spPageContextInfo.siteAbsoluteUrl, {
            [SettingsKeys.SendDepartment]: settings.SendDepartment,
            [SettingsKeys.SendExtendedUserinfo]: settings.SendExtendedUserinfo,
            [SettingsKeys.SendJobTitle]: settings.SendJobTitle,
            [SettingsKeys.SendOffice]: settings.SendOffice,
            [SettingsKeys.SendUserIdEncoded]: settings.SendUserIdEncoded,
            [SettingsKeys.SendUserName]: settings.SendUserName,
            [SettingsKeys.UseSiteCollectionSettings]: settings.UseSiteCollectionSettings
        }));
    }

    if (settings.IsCurrentUserPiwikAdmin) {
        promises.push(setPropertyBag(window.location.origin + Constants.piwikAdminServerRelativeUrl, {
            [SettingsKeys.ContainersUrl]: settings.ContainersUrl,
            [SettingsKeys.EnforceSslComunication]: settings.EnforceSslComunication,
            [SettingsKeys.TemplateSendDepartment]: settings.TemplateSendDepartment,
            [SettingsKeys.TemplateSendExtendedUserinfo]: settings.TemplateSendExtendedUserinfo,
            [SettingsKeys.TemplateSendJobTitle]: settings.TemplateSendJobTitle,
            [SettingsKeys.TemplateSendOffice]: settings.TemplateSendOffice,
            [SettingsKeys.TemplateSendUserIdEncoded]: settings.TemplateSendUserIdEncoded,
            [SettingsKeys.TemplateSendUserName]: settings.TemplateSendUserName,
            [SettingsKeys.Url]: settings.Url,
            [SettingsKeys.UseSha3]: settings.UseSha3
        }));

        promises.push(setSettingsList(window.location.origin + Constants.piwikAdminServerRelativeUrl, Constants.globalSettingsListName, {
            [SettingsKeys.ClientID]: settings.ClientID,
            [SettingsKeys.ClientSecret]: settings.ClientSecret
        }));
    }

    await Promise.all(promises);
};

const getTrackerSettingsWithCaching = async (): Promise<ITrackerSettings> => {
    const storage = new PnPClientStorage();
    return await storage.session.getOrPut(`Piwik:TrackerSettings:${_spPageContextInfo.siteAbsoluteUrl}`, async (): Promise<ITrackerSettings> => {
        const propertyBagPromise = getPropertyBag(_spPageContextInfo.siteAbsoluteUrl);
        const adminPropertyBagPromise = getPropertyBag(window.location.origin + Constants.piwikAdminServerRelativeUrl);

        const propertyBag = await propertyBagPromise;
        const adminPropertyBag = await adminPropertyBagPromise;

        const useSiteCollectionSettings = propertyBag[SettingsKeys.UseSiteCollectionSettings] === 'true';
        const sendExtendedUserinfo = useSiteCollectionSettings
            ? propertyBag[SettingsKeys.SendExtendedUserinfo] === 'true'
            : adminPropertyBag[SettingsKeys.TemplateSendExtendedUserinfo] === 'true';

        const data: ITrackerSettings = {
            ContainersUrl: adminPropertyBag[SettingsKeys.ContainersUrl]
                || adminPropertyBag[SettingsKeys.Url]?.replace(/\.piwik\.pro/gi, ".containers.piwik.pro"),
            IsTrackingActive: propertyBag[SettingsKeys.IsTrackingActive] === 'true',
            SendDepartment: sendExtendedUserinfo && (useSiteCollectionSettings
                ? propertyBag[SettingsKeys.SendDepartment] === 'true'
                : adminPropertyBag[SettingsKeys.TemplateSendDepartment] === 'true'),
            SendJobTitle: sendExtendedUserinfo && (useSiteCollectionSettings
                ? propertyBag[SettingsKeys.SendJobTitle] === 'true'
                : adminPropertyBag[SettingsKeys.TemplateSendJobTitle] === 'true'),
            SendOffice: sendExtendedUserinfo && (useSiteCollectionSettings
                ? propertyBag[SettingsKeys.SendOffice] === 'true'
                : adminPropertyBag[SettingsKeys.TemplateSendOffice] === 'true'),
            SendUserIdEncoded: useSiteCollectionSettings
                ? propertyBag[SettingsKeys.SendUserIdEncoded] === 'true'
                : adminPropertyBag[SettingsKeys.TemplateSendUserIdEncoded] === 'true',
            SendUserName: sendExtendedUserinfo && (useSiteCollectionSettings
                ? propertyBag[SettingsKeys.SendUserName] === 'true'
                : adminPropertyBag[SettingsKeys.TemplateSendUserName] === 'true'),
            SiteId: propertyBag[SettingsKeys.SiteId],
            Url: adminPropertyBag[SettingsKeys.Url],
            UseSha3: adminPropertyBag[SettingsKeys.UseSha3] === 'true'
        };

        return data;
    }, new Date(Date.now() + Constants.trackerSettingsCacheExpirationInMinutes * 60 * 1000));
};

export default {
    getSettings,
    setSettings,
    getTrackerSettingsWithCaching
};