import { ITracker } from './ITracker';

export class ConsoleTracker implements ITracker {
    public async trackPageView(name: string, url: string, data?: {[key: string]: any}): Promise<void> {
        console.log(`[TRACKER] ${name} - URL: ${url}, Data: `, data);
    }

    public async trackEvent(name: string, data?: {[key: string]: any}, duration?: number): Promise<void> {
        console.log(`[TRACKER] Event - Name: ${name}, Duration: ${duration}, Data: `, data);
    }
}
