import { useState, SyntheticEvent, useEffect } from 'react';
import { ISettings } from '../services/ISettings';
import SettingsService from '../services/SettingsService';
import { IChoiceGroupOption } from 'office-ui-fabric-react';

const useSettingsForm = (dismiss: () => void) => {
  const [inputs, setInputs] = useState({} as ISettings);
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [isSaving, setIsSaving] = useState<boolean>(false);
  const [error, setError] = useState<string>(null);

  const loadSettings = async (): Promise<void> => {
    try {
      setError(null);
      setIsLoading(true);
      const settings = await SettingsService.getSettings();
      setInputs(settings);
    }
    catch (err) {
      setError(err.toString());
    }
    finally {
      setIsLoading(false);
    }
  };

  const setSettings = async (settings: ISettings): Promise<void> => {
    try {
      setError(null);
      setIsSaving(true);
      await SettingsService.setSettings(settings);
      dismiss();
    }
    catch (err) {
      setError(err.toString());
    }
    finally {
      setIsSaving(false);
    }
  }

  useEffect(() => {
    loadSettings();
  }, []);

  const handleSubmit = (event: SyntheticEvent) => {
    if (event) {
      event.preventDefault();
    }
    setSettings(inputs);
  }

  const handleInputChange = (event: SyntheticEvent<Element, Event> | undefined) => {
    event!.persist();
    setInputs((inputs: any) => ({ ...inputs, [(event!.target as any).name]: (event!.target as any).value }));
  }

  const handleCheckboxChange = (event: SyntheticEvent<Element, Event> | undefined, checked?: boolean) => {
    event!.persist();
    setInputs((inputs: any) => ({ ...inputs, [(event!.target as any).name]: checked }));
  }

  const handleChoiceGroupChangeBoolean = (ev?: React.FormEvent<HTMLElement | HTMLInputElement>, option?: IChoiceGroupOption): void => {
    ev!.persist();
    setInputs((inputs: any) => ({ ...inputs, [(ev!.target as any).name]: option?.key === 'true' }));
  }

  const setValue = (name: string, value: string) => {
    setInputs((inputs: any) => ({ ...inputs, [name]: value }));
  }

  return {
    inputs,
    isLoading,
    isSaving,
    error,
    handleSubmit,
    handleInputChange,
    handleCheckboxChange,
    handleChoiceGroupChangeBoolean,
    setValue
  };
}

export default useSettingsForm;