import { ITracker } from './ITracker';

export class GlobalArrayTracker implements ITracker {
    private readonly traceLog: { [key: string]: any }[];

    constructor(variableName: string) {
        (window as any)[variableName] = (window as any)[variableName] || [];
        this.traceLog = (window as any)[variableName];
    }

    public async trackPageView(name: string, url: string, data?: { [key: string]: any }): Promise<void> {
        this.fixArrayPush();
        this.traceLog.push({ event: name, url, ...data });
    }

    public async trackEvent(name: string, data?: { [key: string]: any }, duration?: number): Promise<void> {
        this.fixArrayPush();
        this.traceLog.push({ event: name, duration, ...data });
    }

    private fixArrayPush(): void {
        if (!window["sevenTag"]) {
            this.traceLog.push = window.Array.prototype.push;
        }
    };
}
