import React from 'react';
import { Panel, initializeIcons, PanelType } from 'office-ui-fabric-react';
import { useConstCallback } from '@uifabric/react-hooks';
import Form from "./form/Form";

import styles from './App.module.scss';

initializeIcons(undefined, { disableWarnings: true })

function App() {
  const [isOpen, setIsOpen] = React.useState(false);

  const openPanel = useConstCallback(() => setIsOpen(true));
  const dismissPanel = useConstCallback(() => setIsOpen(false));

  window["GeneratePiwikProSettings"] = () => openPanel();

  return (
    <>
      <a onClick={openPanel} className={`o365sx-neutral-accent-link piwik-pro-settings-link ${styles.piwikProLink}`}>Piwik PRO</a>
      <Panel
        headerText="Piwik PRO"
        isOpen={isOpen}
        type={PanelType.medium}
        onDismiss={dismissPanel}
        closeButtonAriaLabel="Close">
          <Form dismiss={dismissPanel}></Form>
      </Panel>
    </>
  );
}

export default App;
