
/** Holds data about a navigation event, leveragingthe html5 browser history API */
export interface INavEvent {
  type: NAV_TYPE;
  /** current page URL hostname */
  origin: string;
  /** current page URL path */
  pathname: string;
  /** current page URL search query => ?, #, structured in an array */
  search: URLSearchParams;
  /** an array of the different query param in the url */
  search_decoded?: {key:string,value:string}[];
}

/** the html5 browser history api differentiates between some type navigation events, when clicking on a html element, using the back/forward browser buttons... */
export enum NAV_TYPE {
  HISTORY,    //0
  CLICK,      //1
  /** Initially state, right after page has been loaded */
  INIT,       //2
  /** represents the spfx page transition event */
  SP_PAGE_TRANSITION //todo: some custom logic may be needed when sp page transtion event is triggered, but url is not changed.
}