import { IContext } from "./IContext";

export class Context implements IContext {
    get siteId(): string {
        return _spPageContextInfo.siteId;
    }
    get siteIdWithoutBrackets(): string {
        return _spPageContextInfo.siteId.replace("{", "").replace("}", "");
    }

    get webId(): string {
        return _spPageContextInfo.webId;
    }
    get webIdWithoutBrackets(): string {
        return _spPageContextInfo.webId.replace("{", "").replace("}", "");
    }

    get webTitle(): string {
        return _spPageContextInfo.webTitle;
    }
    get siteTitle(): string {
        return (_spPageContextInfo as any).siteServerRelativeUrl.split('/').lastItem;
    }

    get userLoginName(): string {
        return _spPageContextInfo.userLoginName;
    }

    get userDisplayName(): string {
        return _spPageContextInfo.userDisplayName;
    }

    get webAbsoluteUrl(): string {
        return _spPageContextInfo.webAbsoluteUrl;
    }

    get webServerRelativeUrl(): string {
        return _spPageContextInfo.webServerRelativeUrl;
    }

    get listUrl(): string {
        return _spPageContextInfo.listUrl;
    }

    get siteAbsoluteUrl(): string {
        return _spPageContextInfo.siteAbsoluteUrl;
    }

    get serverRequestPath(): string {
        return _spPageContextInfo.serverRequestPath;
    }

    get listTitle(): string {
        return _spPageContextInfo.listTitle;
    }

    get listId(): string {
        return _spPageContextInfo.listId.replace("{", "").replace("}", "");
    }
    get pageListId(): string {
        if(_spPageContextInfo.pageListId)
        {
            return _spPageContextInfo.pageListId.replace("{", "").replace("}", "");
        }
        else
        {
            return null;
        }
    }

    get webTemplateConfiguration(): string {
        return (_spPageContextInfo as any).webTemplateConfiguration;
    }

    get pageItemId(): string {
        if(_spPageContextInfo.pageItemId)
        {
            return _spPageContextInfo.pageItemId.toString();
        }
        else
        {
            return null;
        }
    }

    get siteServerRelativeUrl(): string {
        return _spPageContextInfo.siteServerRelativeUrl;
    }
}