import { ITracker } from './ITracker';
import { IContext } from '../contexts/IContext';
import { GlobalArrayTracker } from './GlobalArrayTracker';
import { ConsoleTracker } from './ConsoleTracker';
import { BroadcastingTracker } from './BroadcastingTracker';

export class TrackersProvider {
    constructor(private readonly config: any, private readonly context: IContext) {}

    public getTracker(): ITracker {
        const trackers: ITracker[] = [];

        const globalArray = this.config.trackers
          .find(x => typeof x !== 'string' && x.name === "GlobalArrayTracker");

        if (globalArray && typeof globalArray !== 'string') {
          trackers.push(new GlobalArrayTracker(globalArray.params.variableName));
        }
  
        if (this.config.trackers
          .some(x => x === "ConsoleTracker" || (typeof x !== 'string' && x.name === "ConsoleTracker"))) {
            trackers.push(new ConsoleTracker());
          }
    
        return new BroadcastingTracker(trackers);
    }
}
